<template>
  <div id="personInfo" class="container bc-page">
    <header class="p20-10 tc bc-main">
      <img src="../assets/user.jpg" class="db-ib vm w100 h100 brp50">
    </header>
    <ul class="list">
      <li class="pi-item item mt10">
        <label class="row lh30">
          <span class="dp-ib w70 tr"></span>
          <input class="col c-666" type="text" placeholder="" v-model="user.name">
        </label>
      </li>
      <li class="pi-item item">
        <label class="row lh30">
          <span class="dp-ib w70 tr"></span>
          <input class="col c-666" type="text" placeholder="" v-model="user.sex">
        </label>
      </li>
      <li class="pi-item item">
        <label class="row lh30">
          <span class="dp-ib w70 tr"></span>
          <input class="col c-666" type="text" placeholder="" v-model="user.phone">
        </label>
      </li>
      <li class="pi-item item mt10">
        <label class="row lh30">
          <span class="dp-ib w70 tr"></span>
          <input class="col c-666" type="text" placeholder="" v-model="user.introduce">
        </label>
      </li>
      <li class="pi-item item">
        <label class="row lh30">
          <span class="dp-ib w70 tr"></span>
          <input class="col c-666" type="text" placeholder="" v-model="user.hobby">
        </label>
      </li>
    </ul>
    <div class="m20 pt20">
      <button class="btn btn-full bc-main c-fff" @click="saveInfo"></button>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    name: 'personInfo',
    created: function () {

    },
    data: function () {
      return {
        user: {
          name: 'Tony',
          sex: '',
          phone: 18828282838,
          introduce: 'go! gogo',
          hobby:''
        }
      }
    },
    computed: {},
    methods: {
      saveInfo: function () {
        this.$router.back();
      }
    }
  }
</script>




